import {Component} from '@angular/core';
import {IonicPage, LoadingController, NavParams, ViewController} from 'ionic-angular';
import {VojFormModel} from "../../../models/voj-form.model";
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {VojPageHeaderType} from "../../../models/voj-page-header.model";
import {Configuration} from "../../../config/voj-configuration";
import {MasterdataService} from "../../../providers/masterdata-service/masterdata-service";
import {NationalityDto} from "../../../dtos/nationality.dto";
import {PrefectureDto} from '../../../dtos/prefecture.dto';
import {VojPage} from "../../../models/voj-page";
import {BankSettingsPage} from "../bank-settings/bank-settings";
import {NavigationService} from "../../../services/navigation.service";
import { DatasetType } from '../../../models/dataset-type';
import {ProfileService} from "../../../providers/profile-service/profile-service";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {Storage} from "@ionic/storage";
import {StatusResponseDto} from "../../../dtos/status-response.dto";
import {ObjectUtils} from "../../../utils/utils";

/**
 * Generated class for the PersonalDetailsIdentityPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
	selector: 'page-personal-details-identity',
	templateUrl: 'personal-details-identity.html',
})
export class PersonalDetailsIdentityPage{
	config: VojConfiguration;
	isInscription: boolean;
	formData: VojFormModel;
	headerData: VojPageHeaderType;
    nationalitiesList: NationalityDto[];
    europeanDocsList: Array<DatasetType>;
    foreignDocsList: Array<DatasetType>;
	currentUser: CurrentUserDto;

	constructor(public configurationService: Configuration,
				private masterdataService: MasterdataService,
				public loadingCtrl: LoadingController,
				private navParams: NavParams,
				private navigationService: NavigationService,
				private viewCtrl: ViewController,
				private profileService: ProfileService,
				private storage: Storage) {
		this.config = this.configurationService.configuration;
		this.isInscription = this.navParams.data.isInscription;
		this.formData = {
			fields: [
				{
					type: 'select',
					value: '',
					dataset: [],
					label: 'Nationalit',
					required: !this.isInscription,
					visible: true,
					id: '',
					name: 'nationality',
					action: () => this.checkNationalities()
				},
				{
					type: 'select',
					value: '',
					dataset: [],
					label: 'Type de pice d\'identit',
					required: !this.isInscription,
					visible: true,
					id: '',
					name: 'typeCard',
					action: () => this.checkDocument()
				},
				{
					type: 'text',
					value: '',
					label: 'Numro de pice d\'identit',
					required: !this.isInscription,
					visible: true,
					regex: '^[A-Za-z0-9]*$',
					validationMsg: 'Veuillez ne pas saisir de caractres spciaux ou d\'espaces',
					id: '',
					name: 'cardNumber',
				},
				{
					type: 'date',
					value: '',
					label: 'Dlivre le',
					required: !this.isInscription,
					visible: true,
					id: '',
					name: 'deliveredDate',
					action: () => {
						this.chooseDeliveredDate();
					}
				},
				{
					type: 'select',
					value: '',
					dataset: [],
					label: 'Par',
					required: !this.isInscription,
					visible: true,
					id: '',
					name: 'prefecture',
				},
				{
					type: 'date',
					value: '',
					label: 'Valable du',
					required: !this.isInscription,
					visible: true,
					id: '',
					name: 'validFrom',
                    disabled: true,
                    action: () => {
                        this.chooseValidFromDate();
                    }
				},
				{
					type: 'date',
					value: '',
					label: 'Au',
					required: !this.isInscription,
					visible: true,
					id: '',
					name: 'validTo',
                    disabled: true
				}
			],
			submit: {
				id: '',
				icon: '',
				label: (this.isInscription ? 'Suivant' : 'Valider'),
				action: (event) => {
					console.log('navigation to J-S-GCO-V1-03 Information Resume ');
					this.savePersonalDetailsIdentity();
				}
			}
		};

		this.headerData = {
			title: 'Mon identit',
			actions: [
				{
					id: '',
					icon: 'help',
					label: 'help',
					action: (event) => {
						console.log('help button works')
					}
				}
			],
			style: '',
			isCanceled: false
		};

		// Initialization of documents lists
        this.europeanDocsList = [
            {
                id: 301,
                key: 'CNI',
                value: '1'
            },
            {
                id: 302,
                key: 'Passeport',
                value: '2'
            },
            {
                id: 303,
                key: 'Carte des ressortissants de l\'union europenne',
                value: '3'
            }
		];

		this.foreignDocsList = [
            {
                id: 304,
                key: 'Carte des rsidants',
                value: '4'
            },
            {
                id: 305,
                key: 'Carte de sjour',
                value: '5'
            }
		];
	}

    chooseDeliveredDate() {
		// Change Delivered Date
		if (this.formData.fields[3].value !== '') {
            this.formData.fields[5].disabled = false;
            this.formData.fields[5].min = this.getNextDay(this.formData.fields[3].value);
		}
		// Clear Delivered Date
		else if (this.formData.fields[3].value === '') {
            this.formData.fields[5].disabled = true;
            this.formData.fields[6].disabled = true;
		}
		// Clear next fields date
        this.formData.fields[5].value = '';
        this.formData.fields[6].value = '';
	}

    chooseValidFromDate() {
		console.log('date:', this.formData.fields[5].value);
        // Change Valid From Date
        if (this.formData.fields[5].value !== '') {
            this.formData.fields[6].disabled = false;
            this.formData.fields[6].min = this.getNextDay(this.formData.fields[5].value);
        }
        // Clear Valid From Date
        else if (this.formData.fields[5].value === '') {
            this.formData.fields[6].disabled = true;
        }
        // Clear next field date
        this.formData.fields[6].value = '';
	}

	getNextDay(date): string {
		// Calc next day
		let chosenDate = new Date(date);
        chosenDate.setDate(chosenDate.getDate() + 1);

        let currentMonth = (chosenDate.getMonth() + 1).toString();
        currentMonth = (currentMonth.length === 1) ? '0' + currentMonth : currentMonth;

        let currentDay = chosenDate.getDate().toString();
        currentDay = (currentDay.length === 1) ? '0' + currentDay : currentDay;

		return chosenDate.getFullYear() + '-' + currentMonth + '-' + currentDay;
	}

	checkNationalities() {
		// Reset chosen value of document
        this.formData.fields[1].value = '';
        this.formData.fields[1].dataset = [];

        for (let key in this.nationalitiesList) {
        	// foreign
            if (this.nationalitiesList[key].codeRegion === 3 && this.nationalitiesList[key].gid == this.formData.fields[0].value) {
                this.formData.fields[1].dataset = this.foreignDocsList;
                break;
            }
			// european
            else if (this.nationalitiesList[key].codeRegion !== 3 && this.nationalitiesList[key].gid == this.formData.fields[0].value) {
				this.formData.fields[1].dataset = this.europeanDocsList;
				break;
            }
        }
	}

    checkDocument() {
		// Set max valid length
		// For cni
		if (this.formData.fields[1].value === '1') {
            this.formData.fields[2].max = 12;
		}
		// For passport
		else if (this.formData.fields[1].value === '2') {
			this.formData.fields[2].max = 9;
		}
		// For other types of docs
		else {
            this.formData.fields[2].max = null;
		}
	}

	savePersonalDetailsIdentity(){
		//if all fields are empty, ignore this step and go to the next page
		let ignoreThisStep = !ObjectUtils.isFormNotEmpty(this.formData.fields);
		if(ignoreThisStep){
			this.gotoBankSettings();
			return;
		}

		let identityCardData: any = {
			nationality: this.formData.fields.filter(f => f.name === "nationality")[0].value,
			typeIDCard: this.formData.fields.filter(f => f.name === "typeCard")[0].value,
			numberIDCard: this.formData.fields.filter(f => f.name === "cardNumber")[0].value,
			deliveredDate: this.formData.fields.filter(f => f.name === "deliveredDate")[0].value,
			prefecture:this.formData.fields.filter(f => f.name === "prefecture")[0].value,
			validFrom: this.formData.fields.filter(f => f.name === "validFrom")[0].value,
			validTo: this.formData.fields.filter(f => f.name === "validTo")[0].value
		};

		this.profileService.savePersonalDetailsIdentity(this.currentUser.id, identityCardData).then((data: StatusResponseDto) => {
			if (data.status == "success") {
				if (this.isInscription) {
					this.gotoBankSettings();
				} else {
					//TODO: display a notification of success
				}
			} else {
				console.log("une erreur est survenue lors de l'enregistrement des donnes");
				console.log(data.error);
			}
		});
	}

	gotoBankSettings(){
		this.navigationService.dismiss(this.viewCtrl);
		let vojPage: VojPage = {page: BankSettingsPage, isModal:true, params: {isInscription: this.isInscription}};
		this.navigationService.navigate("main", vojPage);
	}

	ngOnInit() {
		//Show loading spinner
		let loader = this.loadingCtrl.create();
		loader.present();

		//load nationality list
		this.masterdataService.loadNationalitiesList().then((data: NationalityDto[]) => {
            this.nationalitiesList = data;

			//set dataset of nationalities of the form
			this.formData.fields.filter(f => f.name === "nationality")[0].dataset = this.masterdataService.convertNationalitiesListToDataset(data);
			//the default nationality is "France"
			this.formData.fields.filter(f => f.name === "nationality")[0].value = "91";

			// Load Prefecture List
			this.masterdataService.loadPrefectureList().then((data: PrefectureDto[]) => {
				this.formData.fields[4].dataset = this.masterdataService.convertPrefectureListToDataset(data);

				// Hide loading spinner
				loader.dismiss();
			});
		});

	this.storage.get("currentUser").then((val) => {
		this.currentUser = JSON.parse(val);
	});
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad PersonalDetailsIdentityPage');
	}

}



