import { Component, ElementRef } from '@angular/core';
import { IonicPage } from 'ionic-angular';
import { Platform } from 'ionic-angular';
/**
 * Generated class for the VojCalendar component.
 *
 */
@IonicPage()
@Component({
	selector: 'voj-calendar',
	templateUrl: 'voj-calendar.html'
})
export class VojCalendar {
	elRef: ElementRef;
	platform: Platform;

	desktopPlatform: boolean = false;

	constructor(
		elRef: ElementRef,
		platform: Platform
	) {
		this.elRef = elRef;
		this.platform = platform;
		console.log('Hello VojCalendar Component');


		if (this.platform.is('core')) {
			this.desktopPlatform = true;
			console.log('>>>Desktop<<<');
		}
	}

	ionViewWillEnter() {
		// if (this.platform.is('core')) {
		// 	this.desktopPlatform = true;
		// 	console.log('>>>Desktop<<<');
		// }
	}

}



