import {Component, ElementRef} from '@angular/core';
import _ from "lodash";
import {IonicPage, PopoverController} from 'ionic-angular';
import {PopoverPage} from './components/voj-popover.page';

declare let Hammer: any;

/**
 * Generated class for the SmartCalendar component.
 *
 */
@IonicPage()
@Component({
    selector: 'smart-calendar',
    templateUrl: 'smart-calendar.html'
})
export class SmartCalendar {
    elRef: ElementRef;
    popoverCtrl: PopoverController;

    calendarCollapse: boolean = false;
    calendar = {
        mode: 'month',
        currentDate: new Date()
    };
    viewTitle;
    eventSource;
    eventsSource: any[] = [];

    constructor(elRef: ElementRef,
                popoverCtrl: PopoverController) {
        this.elRef = elRef;
        this.popoverCtrl = popoverCtrl;

        this.loadEvents();
    }

    ngAfterViewInit() {
        let hammer = new Hammer(this.elRef.nativeElement.querySelector('calendar'));
        hammer.get('swipe').set({direction: Hammer.DIRECTION_VERTICAL});

        hammer.on('swipe', (event) => {
            this.calendarCollapse ? this.handleSwipeDownEvent() : this.handleSwipeUpEvent();
            this.calendarCollapse = !this.calendarCollapse;
        });
    }

    handleSwipeUpEvent() {
        const rows = this.elRef.nativeElement.querySelectorAll('table tr');
        let selectedIndex: number[] = [];
        if (rows.length > 0) {
            _.forEach(rows, tr => {
                if (tr && tr.children && tr.children.length) {
                    _.find(tr.children, td => {
                        if (td['className'] && td['className'].length) {
                            _.forEach(td['className'].split(' '), (className) => {
                                if (className.slice(10) == 'selected') {
                                    selectedIndex.push(tr.rowIndex);
                                    _.forEach(rows, tr => {
                                        tr.style.display = 'none';
                                        (tr.rowIndex == 0 || tr.rowIndex == selectedIndex[0]) ?
                                            tr.style.display = 'table-row' : false;
                                    })
                                }
                            })
                        }
                    })
                }
            });
        }
    }


    handleSwipeDownEvent() {
        const rows = this.elRef.nativeElement.querySelectorAll('table tr');
        _.forEach(rows, tr => tr.style.display = 'table-row');
    }

    presentPopover(myEvent) {
        let popover = this.popoverCtrl.create(PopoverPage);
        popover.onDidDismiss(data => {
            this.calendar.mode = data.mode;
        });
        popover.present({
            ev: myEvent
        });
    }

    loadEvents() {
        // this.eventSource = this.eventsSource;
        this.eventSource = this.createRandomEvents();
    }

    onViewTitleChanged(title) {
        this.viewTitle = title;
    }

    onEventSelected(event) {
        console.log(event);
        console.log('Event selected:' + event.startTime + '-' + event.endTime + ',' + event.title);
    }

    changeMode(mode) {
        this.calendar.mode = mode;
    }

    today() {
        this.calendar.currentDate = new Date();
    }

    onTimeSelected(ev) {
        // console.log(ev);
        if (this.calendar.mode === 'month') {
            return;
        }

        // let selectedTime: any = new Date(ev.selectedTime);
        let selected: any = Date.parse(ev.selectedTime);

        const event = _.find(ev.events, event => {
            let timeEvent = Date.parse(event['startTime']);
            return timeEvent == selected
        });

        console.log(event);
    }

    createRandomEvents() {
        var events = [];
        for (var i = 0; i < 50; i += 1) {
            var date = new Date();
            var eventType = Math.floor(Math.random() * 2);
            var startDay = Math.floor(Math.random() * 90) - 45;
            var endDay = Math.floor(Math.random() * 2) + startDay;
            var startTime;
            var endTime;
            if (eventType === 0) {
                startTime = new Date(Date.UTC(date.getUTCFullYear(), date.getUTCMonth(), date.getUTCDate() + startDay));
                if (endDay === startDay) {
                    endDay += 1;
                }
                endTime = new Date(Date.UTC(date.getUTCFullYear(), date.getUTCMonth(), date.getUTCDate() + endDay));
                events.push({
                    title: 'All Day - ' + i,
                    startTime: startTime,
                    endTime: endTime,
                    allDay: true
                });
            } else {
                var startMinute = Math.floor(Math.random() * 24 * 60);
                var endMinute = Math.floor(Math.random() * 180) + startMinute;
                startTime = new Date(date.getFullYear(), date.getMonth(), date.getDate() + startDay, 0, date.getMinutes() + startMinute);
                endTime = new Date(date.getFullYear(), date.getMonth(), date.getDate() + endDay, 0, date.getMinutes() + endMinute);
                events.push({
                    title: 'Event - ' + i,
                    startTime: startTime,
                    endTime: endTime,
                    allDay: false
                });
            }
        }
        return events;
    }
}



