import { Component } from '@angular/core';
import { IonicPage } from 'ionic-angular';

@IonicPage()
@Component({
	selector: 'jobyer-availability-choice-page',
	templateUrl: 'jobyer-availability-choice.html'
})
export class JobyerAvailabilityChoicePage {

    dataPageHeader: any;

	constructor(

	) {
		// Set page header title
		this.dataPageHeader = {
            title: 'Add an availability',
            actions:[
                {
                    id : 'help',
                    icon : 'help',
                    action : () => { console.log('Click help button.') },
                    label : ''
                }
            ]
        };
    }

    presentPage(pageName) {
		console.log('Present page:', pageName);
	}

}



