import { Component } from '@angular/core';
import { NavParams } from 'ionic-angular'

import { NotificationType } from '../../models/notification-type.model';

@Component({
	selector: 'voj-notification',
	templateUrl: 'voj-notification.html',
})

export class VojNotification {
	data: NotificationType;

	constructor(
        public params: NavParams
	) {
        this.data = this.params.get('data');
    }

    setModalHeight(): void {

        // Content window
        let vojContents = document.getElementsByClassName('voj-content');
        let modalContent = vojContents[vojContents.length - 1];
        console.log(modalContent.clientHeight);

        // Modal window
        let modalWrapper = document.getElementsByClassName('modal-wrapper')[0];
        console.log(modalWrapper.clientHeight);

        // Set height and show modal
        (modalWrapper as HTMLElement).style.display = 'block';
        (modalWrapper as HTMLElement).style.height = modalContent.clientHeight  + 60 + 'px';

    }

    ionViewDidLoad() {
        this.setModalHeight();
    }

}



