import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';

import { Configuration } from '../../../config/voj-configuration';
import { FormType } from '../../../models/form-type.model';
import { VojPageHeaderType } from '../../../models/voj-page-header.model';
import { VojConfiguration } from '../../../models/voj-configuration.model';

@Component({
    selector: 'availabilities-add-days-page',
    templateUrl: 'availabilities-add-days.html',
})
export class AvailabilitiesAddDaysPage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    formData: FormType;
    isMobile: boolean = false;

    constructor(public configurationService: Configuration,
                public platform: Platform) {
        this.config = this.configurationService.configuration;
        this.config.isLargeScreen ? this.isMobile = false : this.isMobile = true;

        this.headerData = {
            title: 'Add an availability',
            style: 'any',
            actions: [{
                id: 'xdcfgvyhubijnklbhvg',
                label: 'help',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }],
            isCanceled: true
        };

        this.formData = {
            fields: [
                {
                    type: 'checkbox',
                    label: 'Monday',
                    value: false,
                    visible: true,
                    id: '',
                },
                {
                    type: 'checkbox',
                    label: 'Tuesday',
                    value: false,
                    visible: true,
                    id: '',
                },
                {
                    type: 'checkbox',
                    label: 'Wednesday',
                    value: false,
                    visible: true,
                    id: '',
                },
                {
                    type: 'checkbox',
                    label: 'Thursday',
                    value: false,
                    visible: true,
                    id: '',
                },
                {
                    type: 'checkbox',
                    label: 'Friday',
                    value: false,
                    visible: true,
                    id: '',
                },
                {
                    type: 'checkbox',
                    label: 'Saturday',
                    value: false,
                    visible: true,
                    id: '',
                },
                {
                    type: 'checkbox',
                    label: 'Sunday',
                    value: false,
                    visible: true,
                    id: '',
                }
            ],
            submit: {
                id: 'xdcfgvyhubijnkfwlbhvg',
                label: 'Suivant',
                icon: 'help',
                action: function () {
                    console.log('navigate to  J-S-CV-V1-03 Availabilities 1/4')
                }
            }
        }
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad BankSettingsPage');
    }

}



