import {Component} from '@angular/core';
import {NavController, NavParams} from "ionic-angular";
import {VojCardModel} from "../../models/voj-card.model";
import {EventBusProvider} from "../../providers/voj-events/voj-events";
import {NavigationService} from "../../services/navigation.service";
import {VojPageHeaderType} from "../../models/voj-page-header.model";
import {SearchServiceProvider} from "../../providers/search-service/search-service";


@Component({
    selector: 'page-details',
    templateUrl: 'details.html',
})
export class DetailsPage {

    card: VojCardModel;
    headerData: VojPageHeaderType;

    constructor(public navParams: NavParams,
                private searchService : SearchServiceProvider,
                private eventBus : EventBusProvider,
                private navigationService: NavigationService) {
        this.headerData = new VojPageHeaderType();
        this.headerData.title = "Dtails";
        this.card = new VojCardModel();
        this.card.id = 0;
        if(navParams.data && navParams.data.card) {
            this.card = navParams.data.card;
            if(this.searchService.eventMode) {
                this.searchService.markAsRead(this.card);
                this.eventBus.publishAppEventUpdate('read');
            }
        }

    }

    ionViewDidLoad() {

    }


    updateCard(c: VojCardModel) {
        this.card = c;
        if(this.searchService.eventMode) {
            this.searchService.markAsRead(this.card);
            this.eventBus.publishAppEventUpdate('read');
        }
    }

    noData() {
        return !this.card || this.card.id == 0;
    }

    hideDetailsView() {
        this.navigationService.hide('details');
    }
}



