import { ModalController } from 'ionic-angular'
import { Injectable } from '@angular/core';

import { NotificationType } from '../../models/notification-type.model';
import { VojNotification } from './voj-notification';

@Injectable()
export class VojNotificationService {

    constructor(public modalCtrl: ModalController) {}

    // Present notification
    present(notificationData: NotificationType): void {
        // Options for modal window
        let modalOptions = {
            cssClass: 'voj-notification-modal',
            enableBackdropDismiss: notificationData.backdropDismiss
        };

        // Create modal window
        let modal = this.modalCtrl.create(VojNotification, {data: notificationData}, modalOptions);

        // Show modal window
        modal.present();
    }

}



