import {Component} from '@angular/core';

import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojFormModel} from '../../../models/voj-form.model';
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {Configuration} from "../../../config/voj-configuration";
import {AccountService} from "../../../providers/account-service/account-service";
import {NavParams, ViewController} from "ionic-angular";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {Storage} from "@ionic/storage";
import {ModalService} from "../../../services/modal.service";
import {MainAddressPage} from "../main-address/main-address";
import {IdentityDto} from "../../../dtos/identity.dto";
import {StatusResponseDto} from "../../../dtos/status-response.dto";
import {VojNotificationService} from "../../../components/voj-notification/voj-notification.service";
import {NotificationType} from "../../../models/notification-type.model";
import {NavigationService} from "../../../services/navigation.service";
import {VojPage} from "../../../models/voj-page";
import {ObjectUtils, StringUtils} from "../../../utils/utils";

@Component({
	selector: 'identity-page',
	templateUrl: 'identity.html',
})
export class IdentityPage{

	dataPageHeader: VojPageHeaderType;
	formData: VojFormModel;
	config: VojConfiguration;
	currentUser: CurrentUserDto;
	isInscription: boolean;		//flag to know if we are in the inscription process
	isMobile: boolean = false;

	constructor(public configurationService: Configuration,
							public accountService: AccountService,
							public viewCtrl: ViewController,
							private storage: Storage,
							private notification: VojNotificationService,
							private navigationService: NavigationService,
							private navParams: NavParams) {
		this.config = this.configurationService.configuration;
		this.isInscription = this.navParams.data.isInscription;
		this.config.isLargeScreen ? this.isMobile = false : this.isMobile = true;

		// Set page header
		this.dataPageHeader = {
			title: 'Ma civilit',
			actions: [
				{
					id: 'help',
					label: '',
					icon: 'help',
					action: () => {
						console.log('Click help button.')
					}
				}
			],
			style: 'any',
			isCanceled: true
		};

		// Set form data
		this.formData = {
			title: '',
			fields: [
				// Radio for civility
				{
					type: 'radio',
					value: 'Mme',
					label: 'Civilit',
					dataset: [
						{id: 1, key: 'Mme', value: 'Mme'},
						{id: 2, key: 'Mr', value: 'Mr'}
					],
					required: !this.isInscription,
					visible: true,
					id: 'civility',
					name: 'civility',
					action: () => {
						this.checkCivility();
					}
				},
				// Text for name
				{
					placeholder: '',
					type: 'text',
					value: "",
					label: 'Nom',
					required: !this.isInscription,
					readonly: false,
					visible: true,
					min: 0,
					max: 100,
					regex: '^[A-Za-z]*$',
					id: '',
					name: 'lastName'
				},
				// Text for maiden name
				{
					placeholder: '',
					type: 'text',
					value: "",
					label: 'Nom de jeune fille',
					required: false,
					readonly: false,
					visible: true,
					min: 0,
					max: 100,
					regex: '^[A-Za-z]*$',
					id: '',
					name: 'middleName'
				},
				// Text for Prnom
				{
					placeholder: '',
					type: 'text',
					value: "",
					label: 'Prnom',
					required: !this.isInscription,
					readonly: false,
					visible: true,
					min: 0,
					max: 100,
					regex: '^[A-Za-z]*$',
					id: '',
					name: 'firstName'
				},
			],
			submit: {
				id: '',
				icon: '',
				label: (this.isInscription ? 'Suivant' : 'Valider'),
				action: (event) => {
					console.log('Present Information Resume page.')
					this.saveIdentity();

				}
			}
		};
	}

	checkCivility() {
		if (this.formData.fields[0].value === 'Mme') {
			this.formData.fields[2].visible = true;
		}
		else if (this.formData.fields[0].value === 'Mr') {
			this.formData.fields[2].visible = false;
		} else {
			this.formData.fields[2].visible = false;
		}
	}

	ngOnInit() {
		this.storage.get("currentUser").then((data: string) => {
			if (!data) {
				return;
			}
			this.currentUser = JSON.parse(data);

			// Set form data
			this.formData.fields.filter(f => f.name === "civility")[0].value = (ObjectUtils.isEmpty(this.currentUser.identity.civility) ? "Mme" : this.currentUser.identity.civility);
			this.formData.fields.filter(f => f.name === "lastName")[0].value = this.currentUser.identity.lastName;
			this.formData.fields.filter(f => f.name === "middleName")[0].value = this.currentUser.identity.middleName;
			this.formData.fields.filter(f => f.name === "firstName")[0].value = this.currentUser.identity.firstName;
		});
	}

	saveIdentity() {
		//if all fields are empty, ignore this step and go to the next page
		let ignoreThisStep = !ObjectUtils.isFormNotEmpty(this.formData.fields);
		if(ignoreThisStep && this.isInscription){
			this.goToMainAdressPage();
			return;
		}

		//if at least one field is filled, save identity data
		let firstname = this.formData.fields.filter(f => f.name === "firstName")[0].value;
		let identityDto: IdentityDto = {
			civility: this.formData.fields.filter(f => f.name === "civility")[0].value,
			lastName: this.formData.fields.filter(f => f.name === "lastName")[0].value.toUpperCase(),
			middleName: this.formData.fields.filter(f => f.name === "middleName")[0].value.toUpperCase(),
			//first letter in uppercase, like 'Amal'
			firstName: firstname.charAt(0).toUpperCase() + firstname.slice(1)
		};

		this.accountService.saveIdentity(identityDto, this.currentUser.id).then((data: StatusResponseDto) => {
			if (data.status == "success") {
				//save identity data in the local storage
				this.currentUser.identity = identityDto;
				this.storage.set("currentUser", JSON.stringify(this.currentUser));
				//in the case of inscription: we should dismiss the identity modal/page and show the MainAddress modal/page
				if (this.isInscription) {
					this.goToMainAdressPage();
				} else {
					let notif: NotificationType = {
						icon: "star",
						title: "Sauvegarde russie",
						message: "Vos informations ont bien t sauvegardes",
						buttons: [{id: "0", icon: "star", label: "OK", action: this.closeNotif}],
						backdropDismiss: true
					};
					this.notification.present(notif);
				}
			} else {
				console.log("une erreur est survenue lors de l'enregistrement des donnes");
				console.log(data.error);
			}
		});
	}

	/*
	 * Dismissing the current modal (in the case of inscription), and going to the MainAddress page
	 */
	goToMainAdressPage() {
		this.navigationService.dismiss(this.viewCtrl);
		let vojPage: VojPage = {page: MainAddressPage, isModal: true, params: {isInscription: this.isInscription}};
		this.navigationService.navigate("main", vojPage);
	}

	closeNotif() {
		this.viewCtrl.dismiss();
	}
}


