import { Component} from '@angular/core';
import { Platform } from 'ionic-angular';

import { Configuration } from '../../config/voj-configuration';
import { VojPageHeaderType } from '../../models/voj-page-header.model';
import { VojConfiguration } from '../../models/voj-configuration.model';

@Component({
  selector: 'information-resume-page',
  templateUrl: 'information-resume.html',
})
export class InformationResumePage {
  config: VojConfiguration;
  headerData: VojPageHeaderType;
  isMobile: boolean = false;
  informationResumeData: Array<{}>;

  constructor(public configurationService: Configuration,
              public platform: Platform){
    this.config = this.configurationService.configuration;
    this.config.isLargeScreen ? this.isMobile = false : this.isMobile = true;

    this.headerData = {
      title: 'My CV',
      style: 'any',
      actions: [{
            id: 'xdcfgvyhubijnklbhvg',
            label: 'help',
            icon: 'help',
            action: function () {
                console.log('button works')
            }
        }],
      isCanceled: true
    };

    this.informationResumeData = [
        {
          label: 'My jobs',
            action: function () {
                console.log('Redirects to J-S-CV-V1-02 Jobs List')
            }
        },
        {
            label: 'My Availabilities',
            action: function () {
                console.log('Redirects to J-S-CV-V1-03 Availabilities 1/4')
            }
        },
        {
            label: 'My remuneration',
            action: function () {
                console.log('Redirects to Private Link')
            }
        },
        {
            label: 'My qualities',
            action: function () {
                console.log('Redirects to J-S-CV-V1-08 Qualities')
            }
        },
        {
            label: 'My languages',
            action: function () {
                console.log('Redirects to J-S-CV-V1-09 Languages')
            }
        },
        {
            label: 'My Keywords',
            action: function () {
                console.log('Redirects to J-S-CV-V1-10 Key Words')
            }
        },
        {
            label: 'My CV',
            action: function () {
                console.log('Redirects to J-S-CV-V1-11 CV')
            }
        },
        {
            label: 'My CV Vit-On-Job',
            action: function () {
                console.log('Redirects to J-S-CV-V1-12 CV VOJ')
            }
        },


    ]

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad BankSettingsPage');
  }

}



