import {Injectable} from "@angular/core";
import {VojConfiguration} from "../../models/voj-configuration.model";
import {HttpRequestProvider} from "../http-request/http-request";
import {Configuration} from "../../config/voj-configuration";
import {VojTuple} from "../../models/voj-tuple.model";
import {IdentityDto} from "../../dtos/identity.dto";
import {AddressDto} from "../../dtos/address.dto";

@Injectable()
export class AccountService{
	config: VojConfiguration;

	constructor(public http: HttpRequestProvider,
							public configuration: Configuration){
		this.config = this.configuration.configuration;
	}

	authenticate(phone: string, password: string){
		let payload = {
			"service": "AUTH",
			"role": this.config.customer == "J" ? "jobyer" : "employer",
			"phone": phone,
			"password": password
		};
		let url;
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "account") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: any) => {
				console.log(JSON.stringify(data));
				resolve(data);
			});
		});
	}

	saveIdentity(identityDto: IdentityDto, roleId: number){
		let payload = {
			"service": "IDENTITY_SAVE",
			"role": this.config.customer == "J" ? "jobyer" : "employer",
			"civility": identityDto.civility,
			"lastName": identityDto.lastName,
			"middleName": identityDto.middleName,
			"firstName": identityDto.firstName,
			"roleId": roleId.toString()
		};
		//let url = (this.config.urls.filter(tuple => tuple.key === "identity")).value;
		let url;
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "account") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: any) => {
				console.log(JSON.stringify(data));
				resolve(data);
			});
		});
	}

	saveAddress(addressDto: AddressDto, roleId: number){
		let payload = {
			"service": "ADDRESS_SAVE",
			//"id": roleId,
			"id": roleId.toString(),
			"role": this.config.customer == "J" ? "jobyer" : "employer",
			"name": addressDto.name,
			"number": addressDto.number,
			"street": addressDto.street,
			"zipCode": addressDto.zipCode.toString(),
			"city": addressDto.city,
		};
		//let url = (this.config.urls.filter(tuple => tuple.key === "identity")).value;
		let url;
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "account") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: any) => {
				console.log(JSON.stringify(data));
				resolve(data);
			});
		});
	}

	sendPassword(phone: string, email: string){
		let payload = {
			"service": "SEND_PASSWD",
			"role": this.config.customer == "J" ? "jobyer" : "employer",
			"phone": phone,
			"email": email
		};
		let url;
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "account") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: any) => {
				console.log(JSON.stringify(data));
				resolve(data);
			});
		});
	}
}


