import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { IonicPage } from 'ionic-angular';
import _ from "lodash";
/**
 * Generated class for the VojForm component.
 *
 */
@IonicPage()
@Component({
	selector: 'voj-form',
	templateUrl: 'voj-form.html'
})
export class VojForm implements OnInit {

    arePasswordsIdentical: boolean = true; // Passwords identity validation
	selectedItems: any[] = [];
	selectedCustomItems: any[] = [];
	ustomSubItem1: string;
	ustomSubItem2: string;
	showList: boolean = false;
	itemsForAutocomplete: any[];
    dynamicalForm: FormGroup; // Form validation
    submitAttempt: boolean = false; // Form validation
    uploadedItems: any[] = [];

	@Input('data') data;

	constructor(public formBuilder: FormBuilder) {
	}

    checkNgModel(action) {
		// Call action if exists
        if (typeof action !== 'undefined') {
            action();
        }
    }

    checkValidaionAvailability(type): boolean {
        if (type === 'text' || type === 'textarea' || type === 'number' || type === 'tel' || type === 'week' ||
            type === 'url' || type === 'time' || type === 'date' || type === 'color' || type === 'mail' ||
            type === 'month' || type === 'password' || type === 'password-visible' || type === 'search') {
            return true;
        } else {
            return false;
        }
    }

    formValidationInit(fieldsData): void {
        let validationConfig = {};

        for (let i = 0; i < fieldsData.length; i++) {
            // Check available types for validation
            // if (!this.checkValidaionAvailability(fieldsData[i].type)) {
            // 	continue;
            // }

            if(fieldsData[i].dataset3){
                for (let j = 0; j < fieldsData[i].dataset3.length; j++) {
                    let validators = [];
                    console.log('fieldsData[i].dataset3[j]', fieldsData[i].dataset3[j]);
                    if(fieldsData[i].dataset3[j].subdata) {
                        validationConfig[fieldsData[i].dataset3[j].subdata.name1] = ['', Validators.compose(validators)];
                        validationConfig[fieldsData[i].dataset3[j].subdata.name2] = ['', Validators.compose(validators)];
                    }
                }
            }

            let validators = [];

            // Min validation
            if (fieldsData[i].min !== null && typeof fieldsData[i].min !== 'undefined') {
                validators.push(Validators.minLength(fieldsData[i].min));
            }
            // Max validation
            if (fieldsData[i].max !== null && typeof fieldsData[i].max !== 'undefined') {
                validators.push(Validators.maxLength(fieldsData[i].max));
            }
            // Regex validation
            if (fieldsData[i].regex !== null && fieldsData[i].regex !== '' && typeof fieldsData[i].regex !== 'undefined') {
                validators.push(Validators.pattern(fieldsData[i].regex));
            }
            // Required validation
            if (fieldsData[i].required === true) {
                validators.push(Validators.required);
            }


            validationConfig[fieldsData[i].name] = ['', Validators.compose(validators)];
        }

        this.dynamicalForm = this.formBuilder.group(validationConfig);
    }

	ngOnInit() {
        console.log('this.data:', this.data);
        this.formValidationInit(this.data.fields);
	}

	onSubmit(formData, action) {
        console.log("dynamicalForm----->", this.dynamicalForm);
        // Form validation
        this.submitAttempt = true;

        // Passwords identical validation reset
        this.arePasswordsIdentical = true;

        if (!this.dynamicalForm.valid) {
            console.log('Error in validation.');
        } else {
            console.log('Success validation.');
            console.log('dynamicalForm value:', this.dynamicalForm.value);

            // Check password identity
            if (this.dynamicalForm.value.userPassword && this.dynamicalForm.value.passwordConfirmation &&
				(this.dynamicalForm.value.userPassword !== this.dynamicalForm.value.passwordConfirmation)) {
            	// Passwords are not identical
                console.log('Passwords are not identical.');
            	this.arePasswordsIdentical = false;
			} else {
                console.log("Form data:", formData);
                // Call action
                action();
			}
        }
	}

    customSelectValidation(value) {
        // Check for customQualitiesSelect existing
        if ('customQualitiesSelect' in value === false) {
            return false;
        }
        let customSelectExists = 'customQualitiesSelect' in value;
        let selectedItemsLength = this.selectedItems.length;
        return !(customSelectExists === true && selectedItemsLength > 0);
    }

	// Custom Select
	onChangeSelectedItems(selectedValue, index) {
        // Clear select input
        setTimeout(() => {
            this.data.fields[index].value = null;
        });

		if (!_.find(this.selectedItems, item => item == selectedValue)) {
			selectedValue ? this.selectedItems.push(selectedValue) : false;
		}
	}

	deleteSelectedItem(selectedValue) {
		selectedValue ? _.pull(this.selectedItems, selectedValue) : false;
	}

	deleteSelectedCustomItem(selectedValue) {
		selectedValue ? _.pull(this.selectedCustomItems, selectedValue) : false;
	}

	managePassword(value, fieldIndex) {
		// Change password visibility
		value._type === 'password' ?
			this.data.fields[fieldIndex].type = 'password-visible' : this.data.fields[fieldIndex].type = 'password';
	}

	addCustomSelect(val1, val2) {
		if (this.ustomSubItem1 && this.ustomSubItem1.length && this.ustomSubItem2 && this.ustomSubItem2.length) {
			this.selectedCustomItems.push(`${this.ustomSubItem1} - ${this.ustomSubItem2}`);
			val1._value = '';
			val1._text = '';
			val2._value = '';
			val2._text = '';
			this.ustomSubItem1 = '';
			this.ustomSubItem2 = '';
		}
	}

	onSelecteCustomItems(val) {
		if (val.id === 4 || val.id === 5 || val.id === 6) {
			this.ustomSubItem1 = val.value;
		} else {
			this.ustomSubItem2 = val.value;
		}
	}

	/* Custom Input  START*/
	getItems(event: any, sourceArr: any[]) {
		if (!sourceArr) {
			return;
		}
		let val = event.target.value;
		if (val && val.trim() != '') {
			// Filter the items
			this.itemsForAutocomplete = sourceArr.filter((item) => {
				return (item.key.toLowerCase().indexOf(val.toLowerCase()) > -1);
			});
			// Show the results
			this.showList = true;
		} else {
			// hide the results when the query is empty
			this.showList = false;
		}
	}

	selectTip(value, field) {
		value ? field.value = value : false;
		this.showList = false;
	}

	/* Custom Input  END*/

    // Custom Select for Uploading items
    fileEvent(selectedFile) {
        let files = selectedFile.srcElement.files;
        let fileName;
        if (files[0])  {
            fileName = files[0].name;
        }  else {
            return false
        }

        // Encode the file to base64
        let reader = new FileReader();
        reader.readAsDataURL(files[0]);
        reader.onload = function () {
            console.log('Current file in base64:', reader.result);
        };
        reader.onerror = function (error) {
            console.log('Error: ', error);
        };
        if (!_.find(this.uploadedItems, item => item == fileName) && files[0]) {
            fileName ? this.uploadedItems.push(fileName) : false;
        }
    }

    deleteUploadedItem(selectedValue) {
        selectedValue ? _.pull(this.uploadedItems, selectedValue) : false;
    }

    downloadUploadedItem(item) {
        console.log('Downloaded item:', item);
    }
}



