import { Component } from '@angular/core';
import { IonicPage } from 'ionic-angular';

import { VojPageHeaderType } from '../../../models/voj-page-header.model';
import { VojFormModel } from '../../../models/voj-form.model';

@IonicPage()
@Component({
    selector: 'company-page',
    templateUrl: 'company.html',
})
export class CompanyPage {

    dataPageHeader: VojPageHeaderType;
    formData: VojFormModel;
    currentCompanyNumber: string;

    constructor() {
        // Header
        this.dataPageHeader = {
            title: 'COMPANY.TITLE',
            actions: [
                {
                    id: 'help',
                    label: '',
                    icon: 'help',
                    action: () => {
                        console.log('Click help button.')
                    }
                }
            ],
            style: 'any',
            isCanceled: true
        };

        // Form data
        this.formData = {
            title: '',
            fields: [
                // Search button
                {
                    type: 'button',
                    value: '',
                    label: 'COMPANY.SEARCH_BTN',
                    visible: true,
                    id: 'search-button',
                    name: 'searchButton',
                    action: () => {
                        console.log('Click search button');
                    }
                },
                // Text for company name
                {
                    type: 'text',
                    value: '',
                    label: 'COMPANY.COMPANY_NAME',
                    required: true,
                    visible: true,
                    regex: '^[A-z0-9 ]*$',
                    id: 'company-name',
                    name: 'companyName'
                    //validationMsg: 'COMPANY.COMPANY_NAME_ERR'
                },
                // Radio for civility
                {
                    type: 'radio',
                    value: '',
                    label: 'COMPANY.CIVILITY',
                    dataset: [
                        {id: 1, key: 'COMPANY.MRS', value: 'Mrs'},
                        {id: 2, key: 'COMPANY.MR', value: 'Mr'}
                    ],
                    required: true,
                    visible: true,
                    id: 'civility',
                    name: 'civility',
                },
                // Text for user name
                {
                    placeholder: '',
                    type: 'text',
                    value: '',
                    label: 'COMPANY.NAME',
                    required: true,
                    visible: true,
                    regex: '[A-z]*$',
                    id: 'user-name',
                    name: 'userName',
                    validationMsg: 'COMPANY.NAME_ERR'
                },
                // Text for first name
                {
                    placeholder: '',
                    type: 'text',
                    value: '',
                    label: 'COMPANY.FIRST_NAME',
                    required: true,
                    visible: true,
                    regex: '[A-z]*$',
                    id: 'first-name',
                    name: 'firstName',
                    validationMsg: 'COMPANY.FIRST_NAME_ERR'
                },
                // Company number
                {
                    placeholder: '999 999 999 99999',
                    type: 'text',
                    value: '',
                    label: 'COMPANY.COMPANY_NUMBER',
                    required: true,
                    visible: true,
                    regex: '^[0-9 ]*$',
                    id: 'company-number',
                    min: 17,
                    max: 17,
                    name: 'companyNumber',
                    validationMsg: 'COMPANY.COMPANY_NUMBER_ERR',
                    action: () => {
                        this.maskCompanyNumber();
                    }
                },
                // Text for Naf
                {
                    placeholder: '9999A',
                    type: 'text',
                    value: '',
                    label: 'COMPANY.NAF',
                    required: true,
                    readonly: false,
                    visible: true,
                    min: 5,
                    max: 5,
                    regex: '^[0-9]{4}[A-Za-z]{1}',
                    id: 'naf',
                    name: 'naf',
                    validationMsg: 'COMPANY.NAF_ERR'
                }
            ],
            submit: {
                id: '',
                icon: '',
                label: 'COMPANY.VALIDATE_BTN',
                action: () => {
                    console.log('Present Add Adresses page.')
                }
            }
        };

        this.currentCompanyNumber = this.formData.fields[5].value;
    }

    maskCompanyNumber() {
        // Mask company number
        let newValue = this.formData.fields[5].value;

        if (newValue > this.currentCompanyNumber) {
            if (newValue.length === 3 || newValue.length === 7 || newValue.length === 11) {
                this.formData.fields[5].value += ' ';
            } else if ((newValue.length === 4 || newValue.length === 8 || newValue.length === 12) &&
                newValue[newValue.length - 2] !== ' ') {
                this.formData.fields[5].value = newValue.slice(0, newValue.length - 1) + ' ' + newValue[newValue.length - 1];
            }
        } else if (newValue < this.currentCompanyNumber) {
            if (newValue.length === 4 || newValue.length === 8 || newValue.length === 12) {
                this.formData.fields[5].value = this.formData.fields[5].value.slice(0, newValue.length - 1);
            }
        }

        this.currentCompanyNumber = this.formData.fields[5].value;
    }

}



