import { Component } from '@angular/core';

import {Configuration} from '../../../config/voj-configuration';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojConfiguration} from '../../../models/voj-configuration.model';
import {VojHeaderSegmentsType} from "../../../models/voj-header-segments-type";

@Component({
  selector: 'enterprise-search-page',
  templateUrl: 'enterprise-search.html',
})
export class EnterpriseSearchPage {
    config: VojConfiguration;
    enterpriseData: any[];
    headerData: VojPageHeaderType;
    isMobile: boolean = false;
    searchInput: string = '';
    segmentData: VojHeaderSegmentsType;

    constructor(public configurationService: Configuration) {
      this.config = this.configurationService.configuration;
      this.config.isLargeScreen ? this.isMobile = false : this.isMobile = true;
        this.headerData = {
            title: 'Rechercher mon entreprise',
            style: 'any',
            actions: [{
                id: 'gcfhvjbknml',
                label: '',
                icon: 'help',
                action: function showHelp() {
                    console.log('showHelp works')
                }
            }],
            isCanceled: true
        };

        this.segmentData = {
            activeSegmentTab: 0,
            segmentsList: [
                {
                    title: 'Par nom',
                },
                {
                    title: 'Par siren'
                }]
        }
    }

    initializeItems() {
        this.enterpriseData = [
            {
                companyname: 'TestCompany2',
                lastname: 'LastName2',
                firstname: 'FirstName2',
                siren: '765432178',
            },
            {
                companyname: 'TestCompany3',
                lastname: 'LastName3',
                firstname: 'FirstName3',
                siren: '897655638',
            },
            {
                companyname: 'TestCompany1',
                lastname: 'LastName1',
                firstname: 'FirstName1',
                siren: '123456789',
            },
            {
                companyname: 'TestCompany4',
                lastname: 'LastName4',
                firstname: 'FirstName4',
                siren: '123546789',
            },
            {
                companyname: 'TestCompany5',
                lastname: 'LastName5',
                firstname: 'FirstName5',
                siren: '123456798',
            }
        ];

    }

    filterItems() {
        // Reset items back to all of the items
        this.initializeItems();
        // set val to the value of the searchbar

        // Filtering for first segment by name
        if (this.searchInput && this.searchInput.trim() != '' && this.segmentData.activeSegmentTab === 0) {
            console.log('Segment 1 works');
            this.enterpriseData = this.enterpriseData.filter((item) => {
                let result = false;
                    if ((item['firstname'].toLowerCase().indexOf(this.searchInput.toLowerCase()) > -1) ||
                        (item['lastname'].toLowerCase().indexOf(this.searchInput.toLowerCase()) > -1) ||
                         item['companyname'].toLowerCase().indexOf(this.searchInput.toLowerCase()) > -1)   {
                        result = true;
                }
                return result;
            })
        }
        // Filtering for second segment by siren
        else if (this.searchInput && this.searchInput.trim() != '' && this.segmentData.activeSegmentTab === 1) {
            this.enterpriseData = this.enterpriseData.filter((item) => {
                let result = false;
                if ((item['siren'] === this.searchInput)) {
                    result = true;
                }
                return result;
            })
        }
        else {
            this.enterpriseData = [];
        }
    }

    selectedItem() {
        console.log('Navigate tE-S-GCO-V1-04-Enterprise Details 1/3');
    }

    clearEnterpriseData() {
      this.enterpriseData = [];
        this.searchInput = '';
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad EnterpriseSearchPage');
    }

}



