import { Component } from '@angular/core';
import { IonicPage } from 'ionic-angular';

@IonicPage()
@Component({
	selector: 'options-page',
	templateUrl: 'options.html'
})
export class OptionsPage {

    dataPageHeader: any;

	constructor(

	) {
		// Set page header title
		this.dataPageHeader = {
            title: 'Options',
            actions:[
                {
                    id : 'help',
                    icon : 'help',
                    action : () => { console.log('Click help button.') },
                    label : ''
                }
            ]
        };
    }

    presentPage(pageName) {
		console.log('Present page:', pageName);
	}

}



