import {Injectable} from "@angular/core";
import {Events, Nav, Platform, ViewController} from "ionic-angular";
import {Page} from "ionic-angular/navigation/nav-util";
import {VojPage} from "../models/voj-page";
import {VojConfiguration} from "../models/voj-configuration.model";
import {Configuration} from "../config/voj-configuration";
import {LandingPage} from "../pages/landing-page/landing-page";
import {ModalService} from "./modal.service";

@Injectable()
export class NavigationService{
	config: VojConfiguration;
  private nav1: Nav;
	private nav2: Nav;
	private nav3: Nav;

  constructor(public event: Events,
              public configurationService: Configuration,
							public modalService: ModalService){
    this.config = this.configurationService.configuration;
  }

  navigate(navName: string, vojPage: VojPage){
    if(this.config.isLargeScreen){
    	//if the target nav contains only the landing page
			//make the vojPage as a root page instead of landing page
    	let targetNav: Nav = this.getNavByName(navName);
    	if(targetNav.getViews().length == 1 && targetNav.last().component == LandingPage){
				vojPage.isRoot = true;
			}
			if(vojPage.isModal){
				this.modalService.present(vojPage);
			}else {
				this.event.publish("navigate:" + navName, vojPage);
			}
    }else{
			this.event.publish('navigate:main', vojPage);
    }
  }

  hide(navName: string){
    if(this.config.isLargeScreen){
      this.event.publish("hide:" + navName);
    }
  }

  goBack(navName: string){
    if(this.config.isLargeScreen){
      this.event.publish("pop:" + navName);
    }else{
      this.event.publish('pop:main');
    }
  }

	dismiss(viewCtrl: ViewController) {
		if(viewCtrl.isOverlay){
			viewCtrl.dismiss();
		}
	}


  getActiveNav(view: ViewController): Nav{
  	switch(view) {
			case(this.nav1.getByIndex(0)):
				return this.nav1;
			case(this.nav2.getByIndex(0)):
				return this.nav2;
			case(this.nav3.getByIndex(0)):
				return this.nav3;
			default:
				return null;
		}
  }

  private getNavByName(navName: string): Nav{
		switch(navName) {
			case(this.nav1.name):
				return this.nav1;
			case(this.nav2.name):
				return this.nav2;
			case(this.nav3.name):
				return this.nav3;
			default:
				return null;
		}
	}

	constructPageMapping(){
		return {"SearchPage": "searchTab",
			"SignUpPage": "accountTab",
			"LoginPage": "accountTab"};
	}

  /*
  Getters and setters
   */
	getNav1(): Nav {
		return this.nav1;
	}

	setNav1(value: Nav) {
		this.nav1 = value;
	}

	getNav2(): Nav {
		return this.nav2;
	}

	setNav2(value: Nav) {
		this.nav2 = value;
	}

	getNav3(): Nav {
		return this.nav3;
	}

	setNav3(value: Nav) {
		this.nav3 = value;
	}
}


